<template>
  <div>
    <section id="landing" class="landing flex flex-col justify-around">
      <div class="flex flex-col justify-between pb-6 lg:pb-0 lg:pt-6 lg:flex-row-reverse">
        <div class="relative flex justify-end pr-8 mb-4 orbit">
          <div style="position: absolute; width: 100vw">
            <inline-svg :src="require('@/assets/images/ship-path.svg')"></inline-svg>
            <img src="@/assets/images/ship.svg" id="ship" class="w-10 h-auto lg:w-16" />
          </div>
          <img id="planet" src="@/assets/images/planet.svg" class="lg:w-48 lg:mb-10" />
        </div>
        <div class="pb-8">
          <div class="main-text text-5xl font-black lg:text-10xl">
            <div>Explore.</div>
            <div>Play.</div>
            <div>Create.</div>
          </div>
          <div class="secondary-text mt-4 font-thin text-2xl">Let's go to space.</div>
        </div>
      </div>
      <div class="scroll-down font-thin text-sm text-link self-start">scroll down</div>
    </section>
    <section id="about" class="pt-20 pb-32 lg:flex lg:items-center lg:pt-10 lg:pb-40">
      <div class="text-wrapper lg:w-2/5">
        <div class="fade-up font-bold text-2xl mb-4 lg:text-3xl">About us.</div>
        <div class="fade-up font-thin text-xl lg:text-xl">
          We are a creative developer:designer team based on Buenos Aires. We love to explore new paths, play and learn along the way and create cool things. We are
          problem solvers.
        </div>
      </div>
      <div class="astronauts relative flex justify-center mt-24 lg:ml-16 lg:mt-10">
        <div id="left-astronaut">
          <inline-svg :src="require('@/assets/images/astronaut.svg')" class="w-20 h-auto"></inline-svg>
        </div>
        <div id="clap" class="absolute">
          <div class="wrapper">
            <inline-svg :src="require('@/assets/images/clap.svg')" class="w-20 h-auto"></inline-svg>
          </div>
        </div>
        <div id="right-astronaut">
          <inline-svg :src="require('@/assets/images/astronaut.svg')" class="w-20 h-auto svg-fill-secondary transform scale-x-flip"></inline-svg>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import InlineSvg from 'vue-inline-svg'
import { gsap } from 'gsap'
import { MotionPathPlugin } from 'gsap/MotionPathPlugin'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(MotionPathPlugin, ScrollTrigger)

export default {
  name: 'Home',
  components: { InlineSvg },
  mounted() {
    const homeTimeline = gsap.timeline()
    homeTimeline.from('#landing #planet', { opacity: 0, duration: 1, delay: 0.5 })
    homeTimeline.from('#landing .main-text > div', { x: '-150%', stagger: 0.6 })
    homeTimeline.from('#landing .secondary-text', { y: '1rem', opacity: 0, duration: 1 })
    homeTimeline.from('#landing .scroll-down', { opacity: 0, duration: 0.5 })
    gsap.to('#ship', {
      motionPath: {
        path: '#ship-path',
        align: '#ship-path',
        alignOrigin: [0.5, 0.5],
        autoRotate: true,
      },
      duration: 10,
      repeat: -1,
      ease: 'none',
      delay: 3,
    })
    gsap.from('#about .fade-up', {
      scrollTrigger: {
        trigger: '.fade-up',
        start: 'top 80%',
      },
      stagger: 0.2,
      y: 30,
      opacity: 0,
      duration: 0.5,
    })
    gsap.from('#left-astronaut', {
      scrollTrigger: {
        trigger: '#left-astronaut',
        start: 'top bottom',
        end: 'top center',
        scrub: 1,
        // markers: true,
      },
      x: -90,
      ease: 'ease',
      duration: 3,
    })
    gsap.from('#right-astronaut', {
      scrollTrigger: {
        trigger: '#right-astronaut',
        start: 'top bottom',
        end: 'top center',
        scrub: 1,
        // markers: true,
      },
      x: 90,
      ease: 'ease',
      duration: 3,
    })
    gsap.from('#clap .wrapper', {
      rotation: 360,
      duration: 5,
      repeat: -1,
      ease: 'linear',
    })
    gsap.from('#clap', {
      scrollTrigger: {
        trigger: '#clap',
        start: 'top 50%',
        end: 'top 48%',
        scrub: 1.1,
      },
      opacity: 0,
      duration: 5,
    })
  },
}
</script>

<style lang="scss" scoped>
.landing {
  height: calc(var(--vh, 1vh) * 100);
  padding-top: calc(var(--height-nav) + 2rem);
}
#left-astronaut,
#right-astronaut {
  z-index: 2;
}
#clap {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -67%) scale(1);
  z-index: 1;
}
.orbit {
  padding-right: calc(100% / 7);
}
@media (min-width: 500px) {
  .orbit {
    padding-right: calc(100% / 5);
  }
  .main-text {
    font-size: 5rem;
    line-height: 1.5;
  }
  #about {
    margin-left: calc(100% / 5);

    .text-wrapper {
      width: calc(100% / 2);
    }
  }
}
</style>
