<template>
  <nav class="flex py-6 grided justify-between lg:py-12">
    <img
      src="@/assets/logo.svg"
      id="logo"
      alt="Logo"
      class="w-logo lg:w-logob"
      @click="scrollToTop"
    />
    <img
      src="@/assets/icons/menu.svg"
      id="menu-trigger"
      alt="Menu"
      class="w-5 lg:hidden"
      @click="scrollToBottom"
    />
  </nav>
</template>

<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

export default {
  name: 'Navbar',
  mounted() {
    const navTl = gsap.timeline()
    navTl.from('nav #logo', { y: '-2rem', opacity: 0, delay: 0.4 })
    navTl.from('nav #menu-trigger', { scale: 0, duration: 0.4 })
  },
  methods: {
    scrollToTop() {
      document.getElementsByTagName('body')[0].scrollTo({
        top: 0,
        behavior: 'smooth',
      })
    },
    scrollToBottom() {
      document.getElementsByTagName('body')[0].scrollTo({
        top: document.body.scrollHeight,
        behavior: 'smooth',
      })
    },
  },
}
</script>

<style lang="scss" scoped>
nav {
  position: fixed;
  width: 100%;
  z-index: 100;
  border-bottom: 1px solid transparent;
  transition: all 0.3s ease;
  //background: linear-gradient(180deg, white, transparent);
  &.navbar--scrolling {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    background: #fff;
    border-color: #eee;
  }
}
</style>
