<template>
  <div id="app">
    <div id="main-grid">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
    <Navbar :class="{ 'navbar--scrolling': !showNavbar }" />
    <main id="main" class="grided">
      <Home />
    </main>
    <Footer />
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import Footer from '@/components/Footer.vue'
import Home from '@/views/Home.vue'

export default {
  name: 'App',
  components: {
    Navbar,
    Footer,
    Home,
  },
  data() {
    return {
      showNavbar: true,
      lastScrollPosition: 0,
    }
  },
  mounted() {
    document.getElementsByTagName('body')[0].addEventListener('scroll', this.onScroll)
  },
  beforeDestroy() {
    document.getElementsByTagName('body')[0].removeEventListener('scroll', this.onScroll)
  },
  methods: {
    onScroll() {
      const currentScrollPosition = document.getElementsByTagName('body')[0].pageYOffset || document.getElementsByTagName('body')[0].scrollTop
      this.showNavbar = Math.abs(currentScrollPosition - this.lastScrollPosition) < 20

      // if (currentScrollPosition < 0) {
      //   return
      // }
      // // Stop executing this function if the difference between
      // // current scroll position and last scroll position is less than some offset
      // if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 20) {
      //   return
      // }
      // this.lastScrollPosition = currentScrollPosition
      // this.showNavbar = currentScrollPosition < this.lastScrollPosition
    },
  },
}
</script>

<style lang="scss">
#main-grid {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100vw;
  height: calc(var(--vh, 1vh) * 100);
  display: grid;
  pointer-events: none;
  padding: 0 0.9rem;
  grid-template-columns: 1fr;

  & > div {
    display: none;
  }

  & > div:first-child {
    display: block;
    position: relative;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 0;
      height: 100%;
      width: 1px;
      background: rgba(170, 170, 170, 0.42);
      z-index: 200;
      pointer-events: none;
    }

    &:after {
      right: 0rem;
    }

    &:before {
      left: 0rem;
    }
  }

  @media screen and (min-width: 500px) {
    width: calc(100vw - 14px);
    padding: 0 5rem;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    & > div {
      display: block;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 1px;
        background: rgba(170, 170, 170, 0.22);
        z-index: 200;
      }

      &:last-of-type {
        &:after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          height: 100%;
          width: 1px;
          background: rgba(170, 170, 170, 0.22);
          z-index: 200;
        }
      }
    }
  }
}
